/* ========================================= */
* {
  padding: 0px;
  margin: 0px;
}

/* ===daySun animation svg==== */
.daySun {
  animation: daySun 4s infinite, daySunRotate 6s infinite;
  animation-timing-function: linear;
}
@keyframes daySun {
  0% {
    translate: 0px 0px;
  }
  50% {
    translate: 0px 15px;
  }
  100% {
    translate: 0px 0px;
  }
}

@keyframes daySunRotate {
  0% {
    transform: rotateZ(0deg);
  }
  80% {
    transform: rotateZ(0deg);
  }
  100% {
    transform: rotateZ(360deg);
  }
}

